import React from 'react';
import './index.css';
import Dialog from '@material-ui/core/Dialog';
import {MdCancel} from 'react-icons/md' 





         


export class DialogCheck extends React.Component{
    constructor(props){
        super(props);
        this.state={
            text: this.props.text,
            open: this.props.open,
            mobile: this.props.mobile


        }
      
    }


    componentDidUpdate(prevProps){
        if(this.props.open!==prevProps.open){
            this.setState({open:this.props.open})
        }
        if(this.props.text!==prevProps.text){
            this.setState({text:this.props.text})
        }
    }

    dialogText = ()=>{
        return(
            <div className='dialogCheckText'>{this.state.text}</div>
        )
    }
    dialogCallback = ()=>{
        this.props.closed()
    }

    

    render(){
        return(
            <Dialog 
            className={!this.props.mobile?'checkDialog':'checkDialog_mobile'}
            open={this.state.open===true}
            fullScreen>
                <div className='dialogCheckFrame'>
                    <button className='btn_cancel' onClick={()=>{this.dialogCallback()}}><MdCancel size={20}/></button>
                    {this.dialogText()}
                </div>
            </Dialog>
        ); 
    }
}

export default DialogCheck;



  